$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  --mt-footer-padding: 32px 0;
  --mt-footer-margin: null;

  margin: var(--mt-footer-margin);
  padding: var(--mt-footer-padding);
  text-align: right;
}

.mt-footer {
  &__left {
    color: #9a999e;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    font-size: 14px;
  }

  &__middle {
    color: #607b7a;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    font-size: 24px;
    margin: 0 16px;
    padding: 32px 0;
    align-items: center;
  }

  &__right {
    color: #9a999e;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    font-size: 14px;
  }
}
