import { Component, h, Host } from '@stencil/core';

@Component({
  tag: 'mt-footer',
  styleUrl: 'footer.scss',
  scoped: true,
})
export class Footer {
  render() {
    return (
      <Host class="mt-footer">
        <div class="mt-container">
          <span class="mt-footer__left">
            <slot name="start"></slot>
          </span>
          <span class="mt-footer__midle">
            <slot></slot>
          </span>
          <span class="mt-footer__right">
            <slot name="end"></slot>
          </span>
        </div>
      </Host>
    );
  }
}
